"use client";
import { useState, useEffect } from "react";
import { BiSearch } from "react-icons/bi";

export default function PartnerRequestTable() {
  const [requests, setRequests] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);

  useEffect(() => {
    setTimeout(() => {
      const dummy = [
        {id: 1, name: "SUJATA SAWANT", partnerFor: "Food", contactNo: "9773097564", email: "sujataswant77@gmail.com", address: "R.NO.68,Bermacali Railway line,Indira Nagar,Umarwadi Road,Kurla East,Mumbai - 400070 Landmark - Near Kasaiwada", message: "Our ngo working for education and women empowerment...", status: "Approved"},
        {id: 2, name: "DR MUBARAKA KOTAWALA", partnerFor: "Medical", contactNo: "7000462441", email: "drmubaraka.04@gmail.com", address: "Ceradent dental clinic shop no 1 ganga CHS criveni nagar kurar Village Malad East Mumbai 400097", message: "I am a dentist BDS working since 8yrs...", status: "Approved"}
      ];
      setRequests(dummy);
      setLoading(false);
    }, 500);
  }, []);

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Partner Request</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li><a href="/admin/partner-requests" className="hover:underline text-gray-600">Partner Request</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Partner Request Section</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border p-6">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Partner Request</h2>
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-2">
            <select value={entriesPerPage} onChange={(e) => setEntriesPerPage(Number(e.target.value))} className="border rounded-lg px-3 py-2 text-sm">
              <option value={10}>10</option>
            </select>
            <span className="text-sm text-green-600 font-medium">entries per page</span>
          </div>
          <div className="relative">
            <input type="text" placeholder="Search..." value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} className="border rounded-lg pl-4 pr-10 py-2 text-sm w-64"/>
            <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="bg-gray-50 border-b">
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Name</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Partner For</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Contact No.</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Email</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Address Of Operation</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Partner Message</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Status</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Action</th>
              </tr>
            </thead>
            <tbody>
              {requests.map((req, idx) => (
                <tr key={req.id} className={idx % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'}>
                  <td className="px-4 py-3 text-sm">{req.name}</td>
                  <td className="px-4 py-3 text-sm">{req.partnerFor}</td>
                  <td className="px-4 py-3 text-sm">{req.contactNo}</td>
                  <td className="px-4 py-3 text-sm">{req.email}</td>
                  <td className="px-4 py-3 text-sm max-w-xs line-clamp-2">{req.address}</td>
                  <td className="px-4 py-3 text-sm max-w-md line-clamp-3">{req.message}</td>
                  <td className="px-4 py-3"><span className="px-3 py-1 bg-green-100 text-green-700 rounded-full text-xs font-medium">{req.status}</span></td>
                  <td className="px-4 py-3 text-sm text-gray-400">-</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}



